:root {
  --font-body: "Bai Jamjuree", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --strong-cyan: hsl(171, 66%, 44%);
  --light-blue: hsl(233, 100%, 69%);
  --dark-grayish-blue: hsl(210, 10%, 33%);
  --grayish-blue: hsl(201, 11%, 66%);
  --lt-grayish-blue: hsl(201, 11%, 95%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
  font-family: var(--font-body);
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

h1,
h2,
h3 {
  color: var(--dark-grayish-blue);
  line-height: 1.1;
}

h2 {
  font-size: 1.625rem;
  margin-bottom: 1.625rem;
}
@media (min-width: 71.875em) {
  h2 {
    font-size: 2.125rem;
  }
}

h3 {
  font-size: 1.4375rem;
  margin-bottom: 1.25rem;
}

p {
  margin: 0;
}

body {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 1rem;
  color: var(--grayish-blue);
  line-height: 1.8;
  text-align: center;
  background-image: url("../images/bg-header-mobile.png");
  background-repeat: no-repeat;
  background-position: top left;
  background-size: contain;
}
@media (min-width: 40em) {
  body {
    background-image: url("../images/bg-header-desktop.png");
    background-repeat: no-repeat;
    background-position: top left;
    background-size: contain;
  }
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a:link,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 2rem 8.75rem;
}
.container-mw-sm {
  max-width: 43.125rem;
  margin: 0 auto;
}
.container-mw-sm-ptop {
  padding-top: 7.6875rem;
}
.container-mw-md {
  max-width: 56.25rem;
  margin: 0 auto;
}
.container-mw-lg {
  max-width: 69.375rem;
  margin: 0 auto;
}

.flex {
  display: flex;
  flex-flow: column wrap;
}
.flex-jc-c {
  justify-content: center;
}
.flex-ai-c {
  align-items: center;
}
.flex-ac-c {
  align-content: center;
}

.intro__logo {
  display: inline-block;
  margin-bottom: 4.6875rem;
}
.intro__logo img {
  width: 7.875rem;
  height: 7.875rem;
}
@media (min-width: 71.875em) {
  .intro__logo {
    margin-bottom: 4.375rem;
  }
}
.intro > h1 {
  margin-bottom: 1.625rem;
  font-size: 1.875rem;
}
@media (min-width: 71.875em) {
  .intro > h1 {
    font-size: 2.5rem;
  }
}
.intro > p {
  margin-bottom: 3.25rem;
}
@media (min-width: 71.875em) {
  .intro > p {
    font-size: 1.125rem;
  }
}
.intro__button {
  display: block;
  font-weight: 600;
  color: var(--white);
  border-radius: 1.875rem;
  padding: 1rem;
  opacity: 1;
  transition: opacity 200ms ease-in-out;
}
@media (max-width: 39.9375em) {
  .intro__button:not(:last-child) {
    margin-bottom: 1.5625rem;
  }
}
@media (min-width: 40em) {
  .intro__button {
    display: inline-block;
    padding: 0.875rem 2.1875rem;
  }
  .intro__button:not(:last-child) {
    margin-right: 0.9375rem;
  }
}
.intro__button-ios {
  background-color: var(--strong-cyan);
}
.intro__button-mac {
  background-color: var(--light-blue);
}
.intro__button:hover {
  opacity: 0.8;
}

.snippets__header > p {
  margin-bottom: 4.375rem;
}
@media (min-width: 71.875em) {
  .snippets__header > p {
    font-size: 1.125rem;
  }
}
@media (min-width: 71.875em) {
  .snippets__flex {
    display: flex;
    flex-flow: row wrap;
  }
  .snippets__flex-item {
    text-align: left;
    flex: 1;
    align-self: center;
  }
}
.snippets__desktop-img {
  position: relative;
  background-image: url("../images/image-computer.png");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: contain;
  height: 17.9375rem;
  margin-bottom: 3.875rem;
}
@media (min-width: 71.875em) {
  .snippets__desktop-img {
    margin: 0;
    background: none;
    height: 35.75rem;
  }
  .snippets__desktop-img::after {
    content: "";
    position: absolute;
    background-image: url("../images/image-computer.png");
    background-repeat: no-repeat;
    background-position: center center;
    top: 0;
    right: 0;
    bottom: 0;
    left: -20.9375rem;
  }
}
.snippets__subcontent:not(:last-child) {
  margin-bottom: 3.75rem;
}
@media (min-width: 71.875em) {
  .snippets__subcontent {
    margin-left: 4.375rem;
  }
}

.clipboard > p {
  margin-bottom: 4.375rem;
}
@media (min-width: 71.875em) {
  .clipboard > p {
    font-size: 1.125rem;
    margin-bottom: 6.5625rem;
  }
}
.clipboard__img {
  position: relative;
  background-image: url("../images/image-devices.png");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: contain;
  height: 13.75rem;
}
@media (min-width: 71.875em) {
  .clipboard__img {
    background: none;
    height: 29.5rem;
  }
  .clipboard__img::after {
    content: "";
    background-image: url("../images/image-devices.png");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: contain;
    position: absolute;
    top: -5.75rem;
    right: -6.25rem;
    bottom: -5.9375rem;
    left: -6.25rem;
  }
}

.workflow__main-content {
  margin-bottom: 5.875rem;
}
.workflow__sub-content > img {
  height: 2.5rem;
  margin-bottom: 3rem;
}
.workflow__sub-content > h3 {
  margin-bottom: 1.6875rem;
}
@media (max-width: 71.8125em) {
  .workflow__sub-content:not(:last-child) {
    margin-bottom: 3.75rem;
  }
}
@media (min-width: 71.875em) {
  .workflow__flex {
    display: flex;
    flex-direction: row;
    gap: 2.1875rem;
  }
  .workflow__flex-item {
    flex: 1;
  }
}

.company {
  display: flex;
  flex-direction: column;
  gap: 4.1875rem;
}
@media (min-width: 71.875em) {
  .company {
    flex-direction: row;
    gap: 0;
    justify-content: space-between;
    align-items: center;
  }
}

.download > p {
  margin-bottom: 3.25rem;
}
@media (min-width: 71.875em) {
  .download > p {
    font-size: 1.125rem;
  }
}
.download__button {
  display: block;
  font-weight: 600;
  color: var(--white);
  border-radius: 1.875rem;
  padding: 1rem;
  opacity: 1;
  transition: opacity 200ms ease-in-out;
}
@media (max-width: 39.9375em) {
  .download__button:not(:last-child) {
    margin-bottom: 1.5625rem;
  }
}
@media (min-width: 40em) {
  .download__button {
    display: inline-block;
    padding: 0.875rem 2.1875rem;
  }
  .download__button:not(:last-child) {
    margin-right: 0.9375rem;
  }
}
.download__button-ios {
  background-color: var(--strong-cyan);
}
.download__button-mac {
  background-color: var(--light-blue);
}
.download__button:hover {
  opacity: 0.8;
}

.footer_bg {
  background-color: var(--lt-grayish-blue);
}

.footer {
  padding: 3.125rem 0;
}
.footer a {
  color: var(--dark-grayish-blue);
}
@media (max-width: 71.8125em) {
  .footer > div:not(:last-child) {
    margin-bottom: 3rem;
  }
}
.footer__logo {
  font-size: 0;
}
.footer__logo a {
  display: inline-block;
}
.footer__logo img {
  width: 3.5625rem;
}
@media (min-width: 71.875em) {
  .footer {
    display: grid;
    grid-template-columns: 1fr 3fr 2fr;
    justify-items: start;
    align-items: center;
  }
}
.footer__links {
  display: grid;
  grid-template-columns: 1fr;
  gap: 2.0625rem;
}
@media (min-width: 71.875em) {
  .footer__links {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    justify-items: start;
    align-content: space-between;
    column-gap: 4.6875rem;
    row-gap: 0.9375rem;
  }
  .footer__links-faqs {
    grid-column: 1/2;
    grid-row: 1/2;
  }
  .footer__links-contact {
    grid-column: 1/2;
    grid-row: 2/3;
  }
  .footer__links-privacy {
    grid-column: 2/3;
    grid-row: 1/2;
  }
  .footer__links-kit {
    grid-column: 2/3;
    grid-row: 2/3;
  }
  .footer__links-install {
    grid-column: 3/4;
    grid-row: 1/2;
  }
}
.footer__sm-links > a {
  font-size: 1.5625rem;
}
.footer__sm-links > a:not(:last-child) {
  margin-right: 2.25rem;
}
@media (min-width: 71.875em) {
  .footer__sm-links {
    justify-self: end;
  }
}